___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary: #F0ECE5;
  --bg: white;
  --li: #FFF5C2;
  --footer:#161A30;
  --hover: #F12121 ; 
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--bg);
  color: black;
}

a,
input,
button {
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

/* navbar */

.navigasi {
  display: flex;
  justify-content: space-around;
  height: 67px;
  align-items: center;
  background-color: var(--primary);
  z-index: 9999;
  width: 100%;
  position: fixed;
  grid-template-columns: auto 1fr auto;
}

.navigasi .nav_daftar {
  list-style: none;
  display: flex;
  background-color: var(--li); /* Set the background color here */
  border-radius: 8px; /* Add border-radius to round the corners */
  justify-content: space-between;
  width: 40%;
}

.navigasi .nav_daftar .item_nav a {
  color: black;
  display: inline-block;
  font-size: 1rem;
  margin: 0.3rem;
  font-weight: bold;
  text-decoration: none;
  padding: 8px; /* Add padding to create space around the links */
}

.navigasi .nav_daftar .item_nav a:hover,
.navigasi .nav_daftar .item_nav a:active  {
  color: var(--hover);
}

.navigasi .open {
  right: 0;
}

.navbar-extra a:hover {
  color: black;
}

.navbar-extra button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}


/* hero */
.hero {
  min-height: 60vh;
  display: flex;
  align-items: center;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero__image {
  max-width: 350px;
  width: 300px; 
  height: auto;
  object-fit: cover;
  margin-right: 20px;
  margin-left: 20px;
  border-radius: 999px;
}

.hero__content {
  flex: 1;
  color: black; 
  text-align: center;
  margin-left: 20px;
  
}

.hero__title {
  font-family: "laila", serif;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 8px;
  text-align: left;
}

.hero__tagline {
  font-family: "laila", serif;
  font-size: 20px;
  font-weight: bold;
  text-align: left;
}


.btn-cta {
  padding: 0.8rem 2.5rem;
  border: 3px solid transparent;
  border-radius: 999px;
  margin-block-start: 1rem;
  background-color: var(--footer);
  font-family: 'inter', sans-serif;
  text-transform: uppercase;
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--primary);
  cursor: pointer;
  transition: all 0.15s ease-in;
  margin-right: 60rem;
}

.btn-cta:hover {
  background-color: var(--hover); 
}

/* main */
main {
  padding: 32px;
  flex: 1;
}

 .list {
  display: grid;
  margin: 32px 0;
  gap: 18px 16px;
  box-shadow: 2px 4px 2px 4px rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
  grid-template-columns: 1fr; 
  grid-template-rows: auto 1fr;
}

/* template item */

.template-item {
  width: 200px;
  box-shadow: 2px 0 0 4px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
}

.template-item__header {
  position:relative;
}

.wisata-item__image {
  width: 100%;
  height: 200px; 
  object-fit: cover;
}

.template-item_header_rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: rgb(170, 35, 35);
  color: white;
}

.template-item_headerrating_score {
  margin-left: 10px;
}

.template-item__content{
  padding: 16px;
  width: 100%;
}

.template-item__content h3 {
  margin: 0 0 10px 0;
}

.template-item__content h3 a {
  color: #27374D;
  text-decoration: none;
  display: inline-block; 
  width: 100%; 
  height: 44px; 
  margin-right: 40px; 
  padding: 0px;
  margin-top: 20px;
}

.template-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  text-align: justify;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

.wisata-item__image{
  width: 200px;
}


/*Detail Wisata*/

.wisata-detail {
  max-width: 800px;
  box-shadow: 8px 10px 8px 8px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.wisata-detail__name {
  font-size: 24px;
  text-align: center;
  margin-bottom: 10px;
}

.wisata-detail__image {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 20px;
}

.wisata-detail__info {
  line-height: 1.6;
}

.wisata-detail__info h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.wisata-detail__info h2 {
  font-size: 18px;
  margin-top: 15px;
}

.wisata-detail__info p {
  margin-bottom: 10px;
  text-align: justify;
}

/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(177, 7, 120);
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}


/* footer */

footer {
  background-color: var(--footer);
  color: var(--primary);
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  width: 100%;
}


.container h3 {
  margin-bottom: 20px;
  font-size: 20px;
}

.socials {
  height: 100%;
  inset-block: 0;
  inset-inline-end: 0;
  background-color: var(--primary);
  margin-left: 100px;
  border-radius: 999px;
}

.socials a {
  padding: 1.5rem 1rem;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  margin: auto;
  display: flex;  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  transition: all 0.1s ease-in-out;
}

.socials a:hover {
  color: var(--hover);
}

.customer-services {
  flex-grow: 1;
  flex-basis: 100%;
  line-height: 1.9;
  margin-right: 100px;
}

.contact {
  padding-top: 20px;
}

.our-services {
  flex-basis: 100%;
  line-height: 2.3;
  margin-left: 100px;
}


.copyright {
  padding: 15px 0;
  border-top: 1px solid;
}

.skip-to-content {
  position: absolute;
  top: -40px;
  left: 0;
  color: white;
  padding: 8px;
  z-index: 100;
}
.skip-to-content:focus {
  top: 0;
}

.card-img-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4); /* Adjust the last value for transparency */
  color: white; /* Set the text color to white or a contrasting color */
  padding: 15px; /* Add padding as needed */
  box-sizing: border-box; /* Include padding and border in total width/height */
  display: flex;
  flex-direction: column;
  justify-content: flex-end; /* Align text to the bottom */
}

.card-img-overlay h5,
.card-img-overlay p {
  margin: 0; /* Remove default margin to prevent extra spacing */
}

.hero-image{
  width: 510px; 
  height: 312px; 
  object-fit: cover;
}

.detail-image{
  width: 100%;
  height: 400px;
  object-fit: cover;
}

.about-image{
  width: 500px;
}

.tim-image{
  height: 24rem;
  object-fit: cover;
}

@media screen and (max-width: 768px) {
  .hero-image{
    width: 310px; 
    height: 212px; 
    align-self: center;
  }

  .hero-title{
    text-align: center;
  }
  
  .hero-tagline{
    text-align: center;
  }

  .hero-button{
    display: block;
    margin: 0 auto;
    width: 80%;
  }

  .tim-image{
    height: 20rem;
    width: 18rem;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACE,kBAAkB;EAClB,WAAW;EACX,aAAa;EACb,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;EACtB,aAAa;EACb,YAAY;EACZ,qBAAqB;AACvB;;AAEA;EACE,gCAAgC;EAChC,2BAA2B;EAC3B,YAAY;AACd;;AAEA;;;EAGE,qBAAqB;EACrB,eAAe;EACf,gBAAgB;AAClB;;AAEA,WAAW;;AAEX;EACE,aAAa;EACb,6BAA6B;EAC7B,YAAY;EACZ,mBAAmB;EACnB,gCAAgC;EAChC,aAAa;EACb,WAAW;EACX,eAAe;EACf,oCAAoC;AACtC;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,2BAA2B,EAAE,kCAAkC;EAC/D,kBAAkB,EAAE,2CAA2C;EAC/D,8BAA8B;EAC9B,UAAU;AACZ;;AAEA;EACE,YAAY;EACZ,qBAAqB;EACrB,eAAe;EACf,cAAc;EACd,iBAAiB;EACjB,qBAAqB;EACrB,YAAY,EAAE,iDAAiD;AACjE;;AAEA;;EAEE,mBAAmB;AACrB;;AAEA;EACE,QAAQ;AACV;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,6BAA6B;EAC7B,YAAY;EACZ,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;;AAGA,SAAS;AACT;EACE,gBAAgB;EAChB,aAAa;EACb,mBAAmB;EACnB,sBAAsB;EACtB,2BAA2B;EAC3B,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,YAAY;EACZ,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,iBAAiB;EACjB,oBAAoB;AACtB;;AAEA;EACE,OAAO;EACP,YAAY;EACZ,kBAAkB;EAClB,iBAAiB;;AAEnB;;AAEA;EACE,2BAA2B;EAC3B,eAAe;EACf,iBAAiB;EACjB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,2BAA2B;EAC3B,eAAe;EACf,iBAAiB;EACjB,gBAAgB;AAClB;;;AAGA;EACE,sBAAsB;EACtB,6BAA6B;EAC7B,oBAAoB;EACpB,wBAAwB;EACxB,+BAA+B;EAC/B,gCAAgC;EAChC,yBAAyB;EACzB,iBAAiB;EACjB,gBAAgB;EAChB,qBAAqB;EACrB,eAAe;EACf,6BAA6B;EAC7B,mBAAmB;AACrB;;AAEA;EACE,8BAA8B;AAChC;;AAEA,SAAS;AACT;EACE,aAAa;EACb,OAAO;AACT;;CAEC;EACC,aAAa;EACb,cAAc;EACd,cAAc;EACd,8CAA8C;EAC9C,WAAW;EACX,mBAAmB;EACnB,gBAAgB;EAChB,0BAA0B;EAC1B,4BAA4B;AAC9B;;AAEA,kBAAkB;;AAElB;EACE,YAAY;EACZ,0CAA0C;EAC1C,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,YAAY;EACZ,OAAO;EACP,qBAAqB;EACrB,kCAAkC;EAClC,YAAY;AACd;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,WAAW;AACb;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,cAAc;EACd,qBAAqB;EACrB,qBAAqB;EACrB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,uBAAuB;EACvB,oBAAoB;E